<script>
import CrossTable from './components/CrossTable.vue'
export default {
  name: 'App',
  components: { CrossTable },
  data() {
    return { 
      corssTableData: {},
      isEditMode: false,
      isCancelEdit: false,
      isFullScreenMode: false,
      searchDialogVisible: false,
      options: [
        {
          value: '1',
          label: '母公司'
        }, {
          value: '2',
          label: '资管子公司'
        }, {
          value: '3',
          label: '信德子公司'
        }, {
          value: '4',
          label: '谦和子公司'
        }, {
          value: '5',
          label: '香港公司'
        }
      ],
      searchFactor: ''
     }
  },
  created() {
    this.$http.get('./data/data.json').then( res => {
      this.computeTableSize(res.data);
    })
  },
  methods: {
    computeTableSize(raw) {
      // 先把数据拆分成两组
      let sourceArray = [];
      let targetArray = [];

      let rowspanCountLevel1 = 0;
      let rowspanCountLevel2 = 0;
      let rowspanCountLevel3 = 0;
      let rowspanCountLevel4 = 0;
      let colspanCountLevel1 = 1;
      let colspanCountLevel2 = 1;

      raw.forEach( (item, index) => {
        let obj = {
          source: [],
          target: []
        };

        // 添加数据
        Object.assign(item, {
          isEdit: false,
          editedValue: '原始值',
          originalValue: '原始值'
        })
        
        // source 数据分五级
        // target 数据分三级
        for (let key in item) {
          if (key.indexOf('src_') > -1 || key.indexOf('list_') > -1) {
            obj.source[key] = item[key]
          }
          if (key.indexOf('target_') > -1) {
            obj.target[key] = item[key]
          }
        }

        sourceArray.push(obj.source);
        targetArray.push(obj.target);

        // 计算合并行列
        if (index > 0) {
          // 表头第一行计算合并列得数量
          if (item.target_co_id !== raw[index-1].target_co_id) {
            raw[index-1]['col_level1_last'] = colspanCountLevel1;
            colspanCountLevel1 = 1;
          } else if (index === (raw.length - 1)) {
            item['col_level1_last'] = colspanCountLevel1 + 1;
          } else {
            colspanCountLevel1 += 1;
          }
          // 表头第二行计算合并列得数量
          if (item.target_biztype_id !== raw[index-1].target_biztype_id) {
            raw[index-1]['col_level2_last'] = colspanCountLevel2;
            colspanCountLevel2 = 1;
          } else if (index === (raw.length - 1)) {
            item['col_level2_last'] = colspanCountLevel2 + 1;
          } else {
            colspanCountLevel2 += 1;
          }
        }

        // 计算左边固定列合并数量
        // 左边固定列第一列计算合并行数
        // 先判断边界问题
        if (index === 0) {
          rowspanCountLevel1 += 1;
        } else if (index === raw.length - 1){
          raw[index-rowspanCountLevel1]['row_level1_first'] = rowspanCountLevel1 + 1;
        } else {
          if (item.src_co_id !== raw[index-1].src_co_id) {
            raw[index-rowspanCountLevel1]['row_level1_first'] = rowspanCountLevel1;
            rowspanCountLevel1 = 1;
          } else {
            rowspanCountLevel1 += 1;
          }
        }
        // 左边固定列第二列计算合并行数
        if (index === 0) {
          rowspanCountLevel2 += 1;
        } else if (index === raw.length - 1){
          raw[index-rowspanCountLevel2]['row_level2_first'] = rowspanCountLevel2 + 1;
        } else {
          if (item.src_biztype_id !== raw[index-1].src_biztype_id) {
            raw[index-rowspanCountLevel2]['row_level2_first'] = rowspanCountLevel2;
            rowspanCountLevel2 = 1;
          } else {
            rowspanCountLevel2 += 1;
          }
        }
        // 左边固定列第三列计算合并行数
        if (index === 0) {
          rowspanCountLevel3 += 1;
        } else if (index === raw.length - 1){
          raw[index-rowspanCountLevel3]['row_level3_first'] = rowspanCountLevel3 + 1;
        } else {
          let tag = item.src_wall_prjtype_id ? 'src_wall_prjtype_id' : 'src_bizdetail_id';
          if (item[tag] !== raw[index-1][tag]) {
            raw[index-rowspanCountLevel3]['row_level3_first'] = rowspanCountLevel3;
            rowspanCountLevel3 = 1;
          } else {
            rowspanCountLevel3 += 1;
          }
        }
        
        // 左边固定列第四列计算合并行数
        if (index === 0) {
          rowspanCountLevel4 += 1;
        } else if (index === raw.length - 1){
          raw[index-rowspanCountLevel4]['row_level4_first'] = rowspanCountLevel4 + 1;
        } else {
          
          if (item.list_type_code !== raw[index-1].list_type_code) {
            raw[index-rowspanCountLevel4]['row_level4_first'] = rowspanCountLevel4;
            rowspanCountLevel4 = 1;
          } else {
            rowspanCountLevel4 += 1;
          }
        }
      })

      // console.log(sourceArray)
      // console.log(targetArray)

      let sourceData = {};
      let targetData = {};

      // 第一次遍历
      // 组装来源业务部分数据
      sourceArray.forEach( first => {
        if (!sourceData[first.src_co_code]) {
          sourceData[first.src_co_code] = {
            id: first.src_co_id,
            code: first.src_co_code,
            name: first.src_co_name,
            childs: {},
            rowspan: 0
          }
        }
      })
      // 组装影响业务部分数据
      targetArray.forEach( first => {
        if (!targetData[first.target_co_code]) {
          targetData[first.target_co_code] = {
            id: first.target_co_id,
            code: first.target_co_code,
            name: first.target_co_name,
            childs: {},
            colspan: 0
          }
        }
      })

      // 第二次遍历
      // 组装来源业务部分数据
      sourceArray.forEach( second => {
        let $srcTarget = sourceData[second.src_co_code];
        if (!$srcTarget.childs[second.src_biztype_code]) {
          $srcTarget.childs[second.src_biztype_code] = {
            id: second.src_biztype_id,
            code: second.src_biztype_code,
            name: second.src_biztype_name,
            childs: {},
            rowspan: 0
          }
          // $srcTarget.colspan += 1;
        }
      })
      // 组装影响业务部分数据
      targetArray.forEach( second => {
        let $target = targetData[second.target_co_code];
        if (!$target.childs[second.target_biztype_code]) {
          $target.childs[second.target_biztype_code] = {
            id: second.target_biztype_id,
            code: second.target_biztype_code,
            name: second.target_biztype_name,
            last: true,
            childs: [],colspan: 0
          }
        }
      })

      // 第三次遍历
      // 组装来源业务部分数据
      sourceArray.forEach( third => {
        let $srcTarget = sourceData[third.src_co_code].childs[third.src_biztype_code];
        // 防止数据为空
        let tag = false;
        if (third.src_wall_prjtype_code === '') {
          tag = third.src_bizdetail_code
        } else {
          tag = third.src_wall_prjtype_code
        }

        if (!$srcTarget.childs[tag]) {
          $srcTarget.childs[tag] = {
            id: third.src_wall_prjtype_id || third.src_bizdetail_id,
            code: third.src_wall_prjtype_code || third.src_bizdetail_code,
            name: third.src_wall_prjtype_name || third.src_bizdetail_name,
            childs: {},
            rowspan: 0,
          }
          // $srcTarget.colspan += 1;
        }
      })
      // 组装影响业务部分数据
      targetArray.forEach( third => {
        targetData[third.target_co_code].childs[third.target_biztype_code].childs.push(third)
      })

      // 第四次遍历 fourth
      sourceArray.forEach( fourth => {
        if (!fourth.list_type_code) {
          // 限制清单代码 XZ
          // 正常限制清单 XZ.NORMAL
          // 提前限制清单 XZ.ADV
          fourth.list_type_code = 'XZ'
        }

        let $srcTarget = sourceData[fourth.src_co_code].childs[fourth.src_biztype_code];
        
        // 防止数据为空
        let tag = false;
        if (fourth.src_wall_prjtype_code === '') {
          tag = fourth.src_bizdetail_code
        } else {
          tag = fourth.src_wall_prjtype_code
        }

        if (!$srcTarget.childs[tag].childs[fourth.list_type_code]) {
          $srcTarget.childs[tag].childs[fourth.list_type_code] = {
            code: fourth.list_type_code | '子类',
            last: true,
            childs: []
          }
          // $srcTarget.childs[tag].colspan += 1;
        }
      })
      // console.log(sourceData)

      // 第五次遍历 fifth
      sourceArray.forEach( fifth => {
        // 防止数据为空
        let tag = fifth.src_wall_prjtype_code || fifth.src_bizdetail_code;
        // 组装来源业务部分数据
        sourceData[fifth.src_co_code].childs[fifth.src_biztype_code]
          .childs[tag]
          .childs[fifth.list_type_code]
          .childs.push(fifth)
      })

      // console.log('sourceData: ', sourceData)
      // console.log('targetData: ', targetData)

      // 计算要合并的列，下探最底层 childs 数组的长度
      // 来源业务部分
      for (let key1 in sourceData) {
        let level2 = sourceData[key1].childs;
        for (let key2 in level2) {
          let level3 = level2[key2].childs;
          for (let key3 in level3) {
            let level4 = level3[key3].childs;
            for (let key4 in level4) {
              // 最上级长度
              sourceData[key1].childs[key2].rowspan += level4[key4].childs.length;
              sourceData[key1].childs[key2].childs[key3].rowspan += level4[key4].childs.length;
              sourceData[key1].rowspan += level4[key4].childs.length;
            }
          }
        }
      }
      // 影响业务部分
      for (let key1 in targetData) {
        let level2 = targetData[key1].childs;
        for (let key2 in level2) {
          targetData[key1].colspan += level2[key2].childs.length;
        }
      }

      // 按名称排序
      // targetArray.sort((a, b) => {
      //   if (a.target_co_id - b.target_co_id > 0) {
      //     if (a.target_biztype_id - b.target_biztype_id > 0) {
      //       return a.target_activity_id - b.target_activity_id
      //     }
      //   }
      // })
      // console.log(targetArray)

      this.corssTableData = {
        raw: raw,
        source: sourceData,
        target: targetData
      }

      // console.log(this.corssTableData.raw)
    },
    editModeSwitch(isEdit) {
      if (isEdit) {
        this.isEditMode = true;
        this.$message({
          message: '进入编辑模式',
          type: 'warning'
        });
      } else {
        this.isEditMode = false;
        this.$message({
          message: '已保存修改',
          type: 'success'
        });
      }
    },
    cancelEdit() {
      this.$confirm('修改尚未保存，是否保存？', '确认信息', {
        distinguishCancelAndClose: true,
        confirmButtonText: '保存',
        cancelButtonText: '放弃修改'
      })
      .then(() => {
        this.isEditMode = false;
        this.$message({
          message: '已保存修改',
          type: 'success'
        });
      })
      .catch(() => {
        this.isEditMode = false;
      });
    },
    search() {
      this.searchDialogVisible = false;
    }
  }
}
</script>

